.nav-bar{
  display: grid;
  grid-template-columns: auto auto;
  justify-content: space-between;
  background-color: #333;
  align-items: center;
  padding: 0 2rem;
}

.h2-logo{
    color: white;
    font-weight: 400;
    span{
      font-weight: bold;
      margin-right: 3rem;
    }
}
.nav-bar-links{
    padding: .4rem;
    display: flex;
    align-items: center;
    //position:fixed;
    //justify-content: space-between;
}
.link{
  color: rgb(156, 141, 141);
  text-decoration: none;
  margin-right: 1rem;
}
.link-active{
  color: whitesmoke;
  text-decoration: none;
  margin-right: 1rem;
}
.nav-bar__logo{
  max-width: 10rem;
  margin: 1rem 3rem ;
}
